@import "https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;0,500;1,300;1,500&display=swap";
.Header {
  grid-area: Header;
}

.Main {
  grid-area: Main;
  margin-top: 200px;
}

.container {
  grid-template: "Header" .15fr
                 "Main" 1.8fr
                 / 1fr;
  grid-auto-flow: row;
  gap: 0;
  display: grid;
}

html, body, .container {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin: 0;
}

.button {
  fill: #212020;
  height: 50px;
  border: none;
}

h1 {
  font-size: 48px;
  font-weight: 300;
}

h2 {
  font-size: 32px;
  font-weight: 300;
}

h3 {
  font-size: 22px;
  font-weight: 300;
}

h4 {
  font-size: 30px;
  font-weight: 300;
}

h5 {
  color: #fff;
  font-size: 5vw;
}

h6 {
  font-size: 9vw;
}

h7 {
  font-size: 1.8vw;
}

.strong {
  font-weight: bold;
}

body {
  font-family: Lato, serif;
  font-weight: 800;
}

p {
  text-align: left;
  font-size: 2vh;
}

.table {
  border-collapse: collapse;
  width: 100%;
  border: none;
  font-size: 30px;
}

.th, td {
  text-align: left;
  padding: 8px;
}

.thead {
  background-color: none;
}

.tr:nth-child(2n+1) {
  background-color: #6f716f;
}

@keyframes panning {
  0% {
    background-position: 0 0;
  }

  25% {
    background-position: 50%;
  }

  50% {
    background-position: 100% 0;
  }

  75% {
    background-position: 0 100%;
  }

  200% {
    background-position: 0 0;
  }
}

.background {
  height: 100%;
  width: 100%;
  color: #000;
  background-size: cover;
  animation: panning 50s ease-in-out infinite;
  position: absolute;
  left: 0;
}

.background-about {
  height: 120%;
  width: 100%;
  background-size: cover;
  animation: panning 50s ease-in-out infinite;
  position: absolute;
  left: 0;
}

.background-contact {
  height: 110%;
  width: 100%;
  background-size: cover;
  animation: panning 50s ease-in-out infinite;
  position: absolute;
  left: 0;
}

.background-events, .background-volunteer {
  height: 130%;
  width: 100%;
  background-size: cover;
  animation: panning 50s ease-in-out infinite;
  position: absolute;
  left: 0;
}

.background-library {
  height: 140%;
  width: 100%;
  background-size: cover;
  animation: panning 50s ease-in-out infinite;
  position: absolute;
  left: 0;
}

.overlay {
  width: 100%;
  height: 100%;
  z-index: 0;
  background: #0000004d;
  position: absolute;
  top: 0;
  left: 0;
}

.checkbox-round {
  width: 1.8em;
  height: 1.8em;
  vertical-align: middle;
  -webkit-appearance: none;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 50%;
  outline: none;
}

.checkbox-round:checked {
  background-color: #62000d;
}

.search {
  width: 100%;
  display: flex;
  position: relative;
}

.searchTerm {
  width: 100%;
  height: 20px;
  color: #9dbfaf;
  border: 3px solid #fff;
  border-right: none;
  border-radius: 5px 0 0 5px;
  outline: none;
  padding: 5px;
}

.searchTerm:focus {
  color: #e5f8ff;
}

.searchButton {
  width: 40px;
  height: 36px;
  text-align: center;
  color: #000;
  cursor: pointer;
  background: gray;
  border: 1px solid #fff;
  border-radius: 0 5px 5px 0;
  font-size: 20px;
}

.search-bar {
  width: 30%;
  position: absolute;
  top: 50%;
  right: 1%;
  transform: translate(-50%, -50%);
}

input, textarea {
  border: none;
  outline: none;
}

textarea:focus, input:focus {
  border-color: #0000 !important;
}

input::-webkit-input-placeholder {
  color: #bdbdd3;
}

input:-moz-placeholder {
  color: #bdbdd3;
}

input::-moz-placeholder {
  color: #bdbdd3;
}

input:-ms-input-placeholder {
  color: #bdbdd3;
}

textarea::-webkit-input-placeholder {
  color: #bdbdd3;
}

textarea:-moz-placeholder {
  color: #bdbdd3;
}

textarea::-moz-placeholder {
  color: #bdbdd3;
}

textarea:-ms-input-placeholder {
  color: #bdbdd3;
}

.container-contact {
  width: 30%;
  height: auto;
  opacity: .9;
  background-color: #212020;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  margin-left: 2.5%;
  padding: 15px;
  display: flex;
  top: 30%;
}

.contact-form {
  width: 100%;
}

.error {
  color: red;
  border: 1px solid red;
  border-radius: 3px;
  padding: 5px 9px;
}

.success {
  color: green;
  border: 1px solid green;
  border-radius: 3px;
  padding: 5px 9px;
}

.wrap-input {
  width: 100%;
  z-index: 1;
  background-color: #fff;
  border-radius: 7px;
  margin-bottom: 16px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}

.input {
  width: auto;
  color: #212020;
  letter-spacing: .2em;
  height: 42px;
  text-transform: none;
  background: #fff;
  border-radius: 20px;
  padding-left: 10px;
  padding-right: 10px;
  font-family: Lato;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.2;
  display: block;
  position: relative;
}

input.input {
  height: 62px;
  padding: 0 15px;
}

textarea.input {
  min-width: 400px;
  min-height: 429px;
  padding: 19px 15px 0;
}

.container-contact-form-btn {
  flex-wrap: wrap;
  justify-content: center;
  padding-top: 10px;
  display: flex;
}

.contact-form-btn {
  min-width: 250px;
  height: 50px;
  cursor: pointer;
  color: #fff;
  text-transform: uppercase;
  z-index: 1;
  background-color: #0000;
  border-radius: 7px;
  justify-content: center;
  align-items: center;
  padding: 0 20px;
  font-family: Lato;
  font-size: 16px;
  line-height: 1.2;
  transition: all .4s;
  display: flex;
  position: relative;
}

.contact-form-btn:before {
  content: "";
  z-index: -1;
  width: 100%;
  height: 100%;
  pointer-events: none;
  background-color: #6f716f;
  border-radius: 7px;
  transition: all .4s;
  display: block;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.contact-form-btn:hover:before {
  background-color: #62000d;
  outline: none;
}

.contact-info {
  padding-top: 10px;
  padding-right: 20px;
  font-size: 1.8vw;
  display: flex;
  right: 0%;
}

.contact-info-2 {
  display: none;
}

.information {
  text-align: center;
  color: #fff;
  width: 50%;
  height: auto;
  opacity: .9;
  background-color: #212020;
  border-radius: 20px;
  padding-top: 10px;
  padding-left: 20px;
  padding-right: 20px;
  text-decoration-color: #fff;
  position: absolute;
  top: 20%;
  left: 10%;
}

.about_information {
  text-align: center;
  color: #fff;
  width: 40%;
  height: auto;
  opacity: .9;
  background-color: #212020;
  border-radius: 20px;
  padding-top: 10px;
  padding-left: 40px;
  padding-right: 40px;
  text-decoration-color: #fff;
  position: absolute;
  top: 30%;
  left: 5%;
}

.contact_information {
  text-align: center;
  color: #fff;
  height: 100%;
  width: auto;
  opacity: .9;
  background-color: #212020;
  border-radius: 20px;
  padding-top: 10px;
  padding-left: 20px;
  padding-right: 20px;
  text-decoration-color: #fff;
}

.volunteer_information {
  text-align: center;
  color: #fff;
  opacity: .9;
  background-color: #212020;
  border-radius: 20px;
  margin: 0 2.5%;
  padding-top: 10px;
  padding-left: 40px;
  padding-right: 40px;
  text-decoration-color: #fff;
}

.volunteer_donate {
  text-align: center;
  color: #fff;
  opacity: .9;
  background-color: #212020;
  border-radius: 20px;
  padding-top: 10px;
  padding-left: 40px;
  padding-right: 40px;
  text-decoration-color: #fff;
}

.library {
  color: #fff;
  width: auto;
  height: auto;
  opacity: .9;
  background-color: #212020;
  border-radius: 20px;
  margin: 5% 5% 0;
  padding: 10px 40px;
  text-decoration-color: #fff;
  display: flex;
  left: 5%;
  right: 5%;
}

.download-button {
  min-width: auto;
  height: 50px;
  cursor: pointer;
  color: #fff;
  text-transform: uppercase;
  background-color: #62000d;
  border-radius: 7px;
  justify-content: center;
  align-items: center;
  padding: 0 20px;
  font-family: Lato;
  font-size: 16px;
  line-height: 1.2;
  display: block;
}

.donate-download {
  height: auto;
  width: auto;
  z-index: 100;
  top: 180%;
  right: 5%;
}

.library-title {
  color: #fff;
  width: auto;
  height: auto;
  opacity: .9;
  border-radius: 20px;
  padding-top: 10px;
  padding-left: 40px;
  padding-right: 40px;
  text-decoration-color: #fff;
  position: absolute;
  top: 15%;
  left: 5%;
  right: 5%;
}

.title {
  position: absolute;
  left: 5%;
}

.author {
  position: absolute;
  right: 50%;
}

.description {
  right: 10%;
}

.entries {
  width: auto;
  height: auto;
  border-radius: 20px;
  margin: auto;
  padding-left: 40px;
  padding-right: 40px;
  text-decoration-color: #fff;
  position: absolute;
  top: 20%;
}

.responsive-images {
  flex-direction: column;
  margin: 0 2.5%;
  display: flex;
}

.image1-responsive img {
  height: auto;
  width: 600px;
  border-radius: 8px;
  position: relative;
}

.image2-responsive img {
  height: auto;
  width: 600px;
  border-radius: 8px;
  margin-top: 5%;
  position: relative;
}

.video {
  width: 38%;
  height: auto;
  background-color: #212020;
  position: absolute;
  top: 30%;
  right: 5%;
}

.map-responsive {
  margin: 0 2.5%;
  position: relative;
  overflow: hidden;
}

.map-responsive iframe {
  width: 100%;
  height: 100%;
  border-radius: 20px;
}

.inset_1, .inset_1-1 {
  width: 38%;
  height: auto;
  position: absolute;
  top: 80%;
  right: 5%;
}

.inset_2 {
  width: 38%;
  height: auto;
  border-radius: 20px;
  position: absolute;
  top: 30%;
  right: 5%;
}

.inset_3 {
  width: 36%;
  height: auto;
  position: absolute;
  top: 90%;
  right: 5%;
}

.inset_4 img {
  width: 20%;
}

.inset_5 img {
  height: auto;
  border-radius: 8px;
}

.logo-image img {
  float: left;
  width: 175px;
  height: auto;
  padding-top: 10px;
  padding-left: 30px;
  padding-right: 50px;
}

.navbar {
  text-align: center;
  width: 100%;
  height: 130px;
  z-index: 9999;
  background-color: #e5f8ff;
  position: fixed;
  top: 0%;
  left: 0%;
}

.navbar a {
  float: left;
  color: #fff;
  text-align: center;
  z-index: 6;
  padding-left: 75px;
  font-size: 30px;
  font-weight: 500;
  text-decoration: none;
}

.navigation-content {
  fill: #ccc;
  padding: auto;
}

.nav:hover {
  color: #000;
}

navigation-content a:hover {
  color: #e5f8ff;
}

nav.navbar .navigation-content a {
  z-index: 1;
  position: relative;
  overflow: hidden;
}

.red-banner {
  float: right;
  z-index: 1;
  width: 250px;
  height: auto;
  position: fixed;
  top: 115px;
  right: 0;
}

.banner-text {
  color: #fff;
  text-align: center;
  position: absolute;
  transform: translate(40%, -83%);
}

.banner-text-mobile {
  display: none;
}

.contact-section, .donate-section {
  width: 100%;
  justify-content: center;
  display: flex;
}

.library-section {
  width: 95%;
  justify-content: center;
  margin: 0 2.5%;
  display: flex;
}

.about-container {
  margin-top: 10%;
  display: flex;
}

